body {
  padding: 15px 5px;
}

/* Show scrollbars all the time */
::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

label {
  margin: 0 10px;
}

.App {
  text-align: center;
}

.selected-hover {
  cursor: crosshair;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

hr {
  /* border: 1px solid rgb(223, 223, 223); */
  border: none;
  height: 1px;
  background-color: rgb(223, 223, 223);
  color: rgb(223, 223, 223);
}

table {
  border-collapse: collapse;
  padding: 10px;
}

.table-div {
  border: none;
  border-radius: 10px;
  box-shadow: 5px 5px 20px 5px rgba(0, 0, 0, 0.1);
  display: inline-block;
  margin: 10px;
  overflow-y: scroll;
  max-height: 200px;
}

/* button:active {
  background-color: #001d43;
} */

.help-menu {
  position: absolute;
  padding: 10px;
  right: 10px;
  top: 80px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 5px 5px 20px 5px rgba(0, 0, 0, 0.1);
}

#graph-upload-btn:hover, button:hover {
  background-color: #004eb4;
}

button:disabled,
button[disabled]{
  background-color: #cccccc;
  cursor: auto;
}

thead.fixed {
  top: 0;
  z-index: 2;
  position: sticky;
  background-color: white;
}

th[draggable=true]:hover {
  cursor: grab;
}

button, label, input, td, p {
  font-size: 14px;
}

