input {
  margin: 5px 0;
}

button, #graph-upload-btn {
  background-color: #006CF5;
  color: white;
  border-radius: 15px;
  border: none;
  padding: 7px 15px;
  font-weight:600;
  margin-left: 15px;
  box-shadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.1);
  /* Disables default styling (e.g. on iOS). */
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
}

#graph-upload-btn, #graph-download-btn {
  border-radius: 25px;
}

.menu-right {
  float: right;
  margin-right: 10px;
}

.container {
  margin: 30px 10px;
  display: inline-block;
  text-align: center;
  vertical-align: top;
  /* width: 48%; */
}

#queryInput {
  padding: 5px;
  box-shadow: 5px 5px 20px 1px rgba(0, 0, 0, 0.1);
  /* Disables default styling (e.g. on iOS). */
  -webkit-appearance: none;
  appearance: none;
  border: none;
}

th {
  border-bottom: 1px solid lightgray;
  /* border-collapse: collapse; */
}

/* td {
  border-top: 1px solid lightgray;
} */

th, td {
  padding: 10px;
}

.endCell {
  border-left: 1px solid lightgray;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

